/* NavigationMenu.css */

nav {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 10px 0;
    background-color: #c44f4f;
    transition: background-color 0.3s;
  }
  
  nav.scrolled {
    background-color: #8a70cb;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    text-align: center;
  }
  
  ul li {
    display: inline;
  }
  
  ul li a {
    display: inline-block;
    padding: 10px 20px;
    color: #010000;
    text-decoration: none;
    transition: background-color 0.3s, color 0.3s;
  }
  
  ul li a:hover {
    background-color: #555;
  }
  
  .nav-link-active {
    background-color: #555;
  }
  
  body{
    
    background-color: rgb(153, 136, 136);
  }